var _self;
export default {
	data() {
		return {
			userinfo:{},
			avatar:'',
			address:"",
			msg: [],
			nav_list: [
				{
					text: '实时数据',
					rout: 'realtime',
					image: '/static/index/icon-realtime.png'
				},
				{
					text: '监测云图',
					rout: 'cloudmap',
					image: '/static/index/icon-cloudmap.png'
				},
				{
					text: '视频监控',
					rout: 'monitor',
					image: '/static/index/icon-monitor.png'
				},
				{
					text: '分析排名',
					rout: 'analysis',
					image: '/static/index/icon-analysis.png'
				},
				{
					text: '报警管理',
					rout: 'alarm',
					image: '/static/index/icon-alarm.png'
				},
				{
					text: '现场管理',
					rout: 'site',
					image: '/static/index/icon-site.png'
				}
			]
		};
	},
	onLoad() {
		_self = this;
		_self.userinfo = _self.$common.getUserInfo();
		_self.avatar = _self.userinfo.avatar;
		_self.getData()
	},
	methods: {
		// 信息详情
		onReadArticle(id) {
			this.$Router.push({ name: 'article', params: {id:id} });
		},
		//导航页面
		onNav(path_name) {
			this.$Router.push({ name: path_name, params: {} });
		},
		//个人资料
		onUserInfo() {
			this.$Router.push({ name: 'user', params: {} });
		},
		getData(){
			_self.$ajax({
				path: _self.$api.index,
				data:{},
				success(res){
					let { data, code, msg } = res.data;
					_self.msg = data.notice_list
				}
			})
		}
	}
};
